body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
}
.header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
    background-color: #f8f8f8;
}
.logo {
    height: 100px;
}
.search-container {
    flex: 1;
    display: flex;
    justify-content: center;
}
.search-input {
    width: 60%;
    padding: 8px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 4px;
}
.choose-board-button {
    padding: 8px 15px;
    background-color: #4CAF50;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}
.choose-board-button:hover {
    background-color: #45a049;
}

@media (max-width: 768px) {
    .header {
        flex-direction: column;
        text-align: center;
    }

    .search-container {
        width: 100%;
        margin-top: 10px;
    }

    .search-input {
        width: 80%;  
        font-size: 14px;
    }

    .choose-board-button {
        width: 100%;
        margin-top: 10px;
    }
}

@media (max-width: 480px) {
    .search-input {
        width: 90%;
        font-size: 14px;
    }

    .choose-board-button {
        width: 100%;
        padding: 12px;
    }
}

.pins-container {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: 16px;
    padding: 20px;
    margin-top: 20px;
}


.pin {
    position: relative;
    background-color: #f7f7f7;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
    overflow: hidden;
    cursor: pointer;
    transition: transform 0.3s ease;
}

.pin:hover {
    transform: scale(1.05);
}

.pin img {
    width: 100%;
    height: 90%;
    display: block;
    object-fit: cover;
}


.pin-description {
    padding: 10px;
    font-size: 14px;
    color: #333;
    background-color: #f7f7f7;
    text-align: center;
    position: relative; 
}


.circle-btn {
    position: absolute;
    bottom: 70px; 
    left: 90%;
    transform: translateX(-50%); 
    width: 30px;
    height: 30px; 
    background-color: #96b397c7; 
    border: none;
    border-radius: 50%; 
    cursor: pointer;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); 
    transition: background-color 0.3s ease;
}

.circle-btn:hover {
    background-color: #45a049; 
}

@media (max-width: 768px) {
    .pins-container {
        grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    }
}